import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GetPhotoGalleryDetailsService } from './galleryDetailsService';
import { UtilService } from 'src/providers/utils/utils.service';

@Component({
  selector: 'app-photo-gallery-details',
  templateUrl: './photo-gallery-details.page.html',
  styleUrls: ['./photo-gallery-details.page.scss'],
})
export class PhotoGalleryDetailsPage implements OnInit {

  loaded: boolean = false;

  photosList = [];

  selectedPhotoId:any;

  constructor(private router: Router, public getPhotoGalleryDetailsService : GetPhotoGalleryDetailsService, public utils: UtilService) { 
    this.selectedPhotoId = history.state.photoID;
  }

  ngOnInit() {
    this.getPhotoGalleryDetails();
  }

  /**
   * Method used to repalce 'bpp1.' with ''
   * @param link - Image Url that need to be changed
   */
  replaceURL(link: any) {
    // if (link != null && link != undefined) {
    //   link = link.replace('bpp1.', '');
      return link;
    // }
  }

  navToImagePage(i: any) {
    this.router.navigate(["/show-gallery-photo"], { state: { index : i, list: this.photosList }});
  }

  getPhotoGalleryDetails() {
    this.utils.showProgressLoader();
    this.getPhotoGalleryDetailsService.getPhotoGalleryDetails(this.selectedPhotoId).then(
      (result) => {
        try {
          this.utils.dissmisProgressLoader();
          this.photosList = result['responseJSON']['array'];
      } catch (e) {
         this.utils.dissmisProgressLoader();
        }
      },
      (err) => {
      alert("error");
      }
    );
  }

}
